import '../css/quickorder.scss';

import Class from './modules/core/Class';
import Module from './modules/core/Module';
import View from './modules/core/View';
import Model from './modules/core/Model';
import Events from './modules/core/Events';
import ImageService from './services/ImageService';
import Notifier from './modules/ui/Notifier';
import SkuItemService from './services/SkuItemService';

var conf = {
    dialog: {
        NO_ITEM_SELECTED: 'Please select an item to add to your cart',
        SKU_NOT_VALID: 'This is not a valid sku',
        GENERIC_SERVICE_ERROR: 'An error occured while attempting to perfom this action. Please try again later.'
    }
};
conf.action = {
    add_multi: upi.paths.STORE_URL + '?items=',
    add_quick_list: upi.paths.STORE_URL + '?savedCartID='
};

/* Base Class for all QuickOrder modules
 * Add basic helper methods for interacting with the cart from upi.webStore
 * and handling changes in the UI
 */
var QuickOrderCore = Class.extend( $.extend( {}, Module, Events ) );

var QuickOrderCart = QuickOrderCore.extend( {

    elements: {
        '.form': 'form',
        '.form li': 'field_list',
        '.button.addToCart': 'buttons'
    },

    events: {
        'click .addToCart:not(.disabled):not(.submitted)': 'add_to_cart'
    },

    parse_form: function () {
        var result = {};

        this.field_list.each( function ( i, item ) {
            var inputs = $( item ).find( 'input' ),
                sku = inputs.filter( '[name="sku"]' )[0].value.toUpperCase(),
                quan = parseInt( inputs.filter( '[name="qty"]' )[0].value, 10 );

            if ( sku && quan > 0 ) {
                result[sku] = quan;
            }
        } );
        return this.serialize( result ) || null;
    },

    get_templates: function () {
        // exit if templates already loaded
        if ( $( '#_x-javascript-cart-templates' ).length ) {
            return;
        }

        $.get( '/scripts/templates/cart.html', function ( data ) {
            $( '<div id="_x-javascript-cart-templates"></div>' ).html( data ).appendTo( 'body' );
        } );
    },

    serialize: function ( obj ) {
        var result = '';

        for ( var key in obj ) {
            if ( obj.hasOwnProperty( key ) ) {
                result += ((result == '')
                    ? ''
                    : '|') + key + ',' + obj[key];
            }
        }

        return encodeURI( result );
    },

    add_to_cart: function ( e ) {
        var params = this.parse_form();
        if ( params !== null ) {
            this.trigger( 'cart:addtocart' );
            window.location = conf.action.add_multi + params;
        }
        else {
            $( e.target ).showHint( conf.dialog.NO_ITEM_SELECTED );
        }
    },

    toggle_button: function ( e ) {
        $( '.button.addToCart' ).addClass( 'submitted' ).text( 'Submitting...' );
    },

    enable_buttons: function ( is_valid ) {
        var btns = this.buttons;
        (is_valid)
            ? btns.removeClass( 'disabled' )
            : btns.addClass( 'disabled' );
    },

    /**
     * setTimeoutHandler
     * is triggered for failed responses, we resort to a timeout handler instead
     */
    setTimeoutHandler: function () {
        return setTimeout( function () {
            // Notifier.error( conf.dialog.GENERIC_SERVICE_ERROR );
            Notifier.prompt();
        }, 3500 );
    }

} );

/* Model for ShopBySku */
var QuickListSku = function () {
    return Model.extend( {
        get_thumbnail: function () {
            // refactor by enforcing sku "-" on model
            var skuId = this.id;
            if ( skuId.indexOf( '-' ) === -1 ) {
                skuId = skuId + '-';
            }
            return ImageService.getSizedUrlForSku( skuId, {type: 'medium'} );
        }
    } );
};

const ShopBySku = window.ShopBySku = QuickOrderCart.extend( {
    elements: {
        '#addbysku-sku-num': 'sku_field',
        '#addbysku-qty': 'qty_field',
        '#shopBySkuList': 'sku_list',
        '#shopBySkuList .button[data-action^="edit"]': 'edit_button',
        '.form': 'form',
        '.form li': 'field_list',
        '.button.addToCart': 'buttons'
    },

    events: {
        'keypress qty_field': 'add_lineitem',
        'keypress sku_field': 'add_lineitem',
        'focus sku_field': 'handle_mousewheel',
        'blur sku_field': 'handle_mousewheel',
        'click edit_button': 'edit_lineitem',
        'keypress .editmode input.qty': 'update_lineitem',
        'blur .editmode input.qty': 'update_lineitem',
        'keypress .add': 'add_lineitem',
        'click .add': 'add_lineitem',
        'click .addToCart:not(.disabled):not(.submitted)': 'add_to_cart',
        'click .remove': 'remove_lineitem'
    },

    // Sku object: passed to sku item template
    Sku: new QuickListSku,

    init: function ( el ) {
        this.Sku.attributes = ['id', 'name', 'qty', 'thumb_url'];
        this.Sku.load_local( 'shop_by_sku' );
        this.view = new View( 'addbyskuLineItem' );

        this.ready( function () {
            var self = this;
            this.view.whenTemplatesLoaded.then( function () {

                // load from localStorage
                $.each( self.Sku.all(), self.proxy( function ( i, sku ) {
                    if ( sku.qty <= 0 ) {
                        sku.destroy();
                    }

                    // refresh sku data to update instock info
                    this._getSkuInfo( sku.id )
                        .then( data => {
                            var skuData = data.Items[0];
                            sku.inStock = skuData.InStock;
                            sku.estimatedShipDate = skuData.EstimatedShipDate;
                            sku.estimatedShipDateFormatted = upi.Date.format( skuData.EstimatedShipDate );

                            self.render_sku( sku );
                        } );

                } ) );
            } );

            this.buttons.addClass( 'disabled' );

            /* Event bindings */
            this.bind( 'cart:updatesku', ( e, data ) => {
                var sku = this.Sku.find( data.id );
                if ( sku ) {
                    sku.qty = data.qty;
                    sku.save();
                    this.update_sku( sku );
                }
            } );

            this.bind( 'cart:removesku', ( e, data ) => {
                var sku = this.Sku.find( data.id );
                if ( sku ) {
                    sku.destroy();
                }
            } );

            this.bind( 'cart:addsku', ( e, sku ) => {
                this.add_sku( sku );
            } );

            this.bind( 'cart:addskusuccess cart:updateskusuccess', ( e, sku ) => {
                this.sku_field.val( '' ).removeClass( 'error' ).focus();
                this.qty_field.val( '' );
            } );

            this.bind( 'cart:addskucomplete cart:removesku', ( e, sku ) => {
                this.update_elements();
                this.validate();
            } );

            // save to local storage
            this.bind( 'cart:addsku cart:removesku cart:updatesku cart:clear', () => {
                this.Sku.save_local( 'shop_by_sku' );
            } );

            this.bind( 'cart:addtocart', this.proxy( this.toggle_button ) );
        } );

        return this;
    },

    handle_mousewheel: function ( e ) {

        switch ( e.type ) {
            case 'focusin':
                // cancel number spinner on mousewheel events
                $( e.target ).on( 'mousewheel', function ( e ) {
                    e.preventDefault();
                } );
                break;
            case 'focusout':
                $( e.target ).off( 'mousewheel' );
                break;

        }
    },

    search_sku: function ( sku ) {
        if ( !sku ) {
            return false;
        }

        return this._getSkuInfo( sku )
            .then( data => {
                // return the sku
                return data.Items[0];
            } )
            .catch( err => {
                this.sku_field.focus()
                    .addClass( 'error' )
                    .showHint( conf.dialog.SKU_NOT_VALID, {hide_trigger: 'keydown focusout'} );

                return Promise.reject( conf.dialog.SKU_NOT_VALID );
            } )

    },

    _getSkuInfo: function ( sku ) {
        return SkuItemService.fetch( sku.trim().toUpperCase() );
    },

    add_sku: function ( sku ) {
        var existing_sku = this.Sku.find( sku.id.toString().toUpperCase() );

        // if sku is already in list, merge quantities and flash lineitem
        if ( existing_sku ) {
            existing_sku.qty += sku.qty;
            this.trigger( 'cart:updatesku', existing_sku );
        }
        else {
            var new_sku = new this.Sku( {
                id: sku.id.toString().toUpperCase(),
                name: sku.KitName,
                qty: sku.qty,
                inStock: sku.InStock,
                estimatedShipDate: sku.EstimatedShipDate || null,
                estimatedShipDateFormatted: upi.Date.format( sku.EstimatedShipDate ) || null
            } );
            this.render_sku( new_sku );
        }

        this.trigger( 'cart:addskusuccess' );
    },

    render_sku: function ( sku ) {
        var lineitem = $( this.view.render( sku ) ).hide();
        this.sku_list.append( lineitem );

        sku.view = lineitem;
        lineitem.slideDown( 200, this.proxy( function () {
            this.trigger( 'cart:addskucomplete', sku );
        } ) );
    },

    update_sku: function ( sku ) {
        var lineitem = this.el.find( '#lineitem-' + sku.id );
        lineitem.find( '[name="qty"]' ).val( sku.qty );
        lineitem.find( 'div.qty' ).text( sku.qty );

        sku.view = lineitem;
        lineitem.highlight( 'lightyellow', 800, this.proxy( function ( e ) {
            this.trigger( 'cart:addskucomplete', sku );
        } ) );

        this.trigger( 'cart:updateskusuccess' );
    },

    add_lineitem: function ( e ) {
        if ( e && (e.type == 'keyup' || e.type == 'keypress') && !Events.is_enter_key( e ) ) {
            return;
        }

        var sku_data = {},
            sku_id = this.sku_field.val(),
            qty = parseInt( this.qty_field.val(), 10 );

        // if sku search success: addsku, else fail silent
        this.search_sku( sku_id ).then( sku => {
            sku_data = $.extend( {}, sku, {
                id: sku_id,
                qty: qty || 1,
                name: sku.KitName
            } );
            // scroll to last item in sku list
            this.sku_list.find( '.lineItem:last' ).scrollToElement( {delay: 2000} );
            this.trigger( 'cart:addsku', sku_data );
        } );

        if ( Events.is_enter_key( e ) ) {
            // prevent "Ding!" in IE
            return false;
        }
    },

    remove_lineitem: function ( e ) {
        var el = e.target || e;

        if ( !$( el ).hasClass( 'lineItem' ) ) {
            el = $( el ).parent( '.lineItem' );
        }

        var id = el.find( 'input[name="sku"]' )[0].value;

        el.slideUp( 200, this.proxy( function () {
            el.remove();
            this.trigger( 'cart:removesku', {id: id} );
        } ) );
    },

    edit_lineitem: function ( e ) {
        var lineitem = $( e.target ).parents( '.lineItem' );
        this.toggle_lineitem( lineitem, 'edit' );
    },

    update_lineitem: function ( e ) {
        // TODO: refactor this method to separate MODEL/VIEW
        //
        if ( e.type != 'focusout' && !Events.is_enter_key( e ) ) {
            return;
        }

        var lineitem = $( e.target ).parents( '.lineItem' ),
            id = lineitem.find( 'input[name="sku"]' )[0].value,
            qty = parseInt( lineitem.find( 'input.qty' ).val(), 10 );

        this.toggle_lineitem( lineitem, 'update', qty );

        // if qty field 0 or blank remove item
        if ( qty === 0 || isNaN( qty ) ) {
            this.remove_lineitem( lineitem );
        }

        this.trigger( 'cart:updatesku', {
            id: id,
            qty: qty
        } );

        return false;
    },

    clear_list: function () {
        this.Sku.destroy();
        this.trigger( 'cart:clear' );
    },

    toggle_lineitem: function ( item, action, qty ) {
        var id = item.attr( 'id' ).replace( /lineitem-/, '' ),
            btn = item.find( '.button[data-action]' );

        switch ( action ) {
            case 'edit':
                item.addClass( 'editmode' );
                item.find( 'input.qty' ).focus();
                break;

            case 'update':
                item.removeClass( 'editmode' );
                item.find( 'div.qty' ).text( qty || '0' );
                break;

            default:
                return;
        }
    },

    add_to_cart: function ( e ) {
        var self = this;

        // if addToCart clicked while sku_field is populatd attempt to validate sku first, then re-execute this method
        if ( this.sku_field.val() !== '' ) {
            this.one( 'cart:addskucomplete', function () {
                self.add_to_cart( e );
            } );

            this.add_lineitem();
            return false;
        }
        this._super( e );
        this.clear_list();
    },

    validate: function () {
        var is_valid = false;
        // if at least one set of fields have both contain values consider valid
        this.field_list.each( function ( i, item ) {
                var fields = $( item ).find( 'input' );
                if ( fields.length > 0 && (fields[0].value != '' && fields[1].value != '') ) {
                    is_valid = true;
                }
            }
        );
        this.enable_buttons( is_valid );

        return is_valid;
    }

} );

const RecentPurchases = window.RecentPurchases = QuickOrderCart.extend( {

    elements: {
        '.form': 'form',
        '.form li': 'field_list',
        'input[name="qty"]': 'qty_fields',
        '.button.addToCart': 'buttons'
    },

    events: {
        'focus input': 'toggle_item',
        'blur input': 'toggle_item',
        'keyup input': 'validate',
        'change input': 'validate',
        'click .addToCart:not(.disabled):not(.submitted)': 'add_to_cart'
        //'click .showMore': 'show_more_results'
    },

    init: function ( el ) {
        this.ready( function () {
            this.buttons.addClass( 'disabled' );
            this.bind( 'cart:addtocart', this.proxy( this.toggle_button ) );
        } );

    },

    toggle_item: function ( e ) {
        var line_item = $( e.target ).parents( '.lineItem' );
        switch ( e.type ) {
            case 'focusin':
                line_item.addClass( 'active' );
                break;
            case 'focusout':
                if ( e.target && e.target.value == '' ) {
                    line_item.removeClass( 'active' );
                }
        }
    },

    validate: function () {
        var is_valid = false;
        // if at least one qty field not blank, is_valid
        this.qty_fields.each( function () {
                var field = $( this );
                if ( field.length > 0 && (field[0].value != '' && parseInt( field[0].value, 10 ) > 0) ) {
                    is_valid = true;
                }
            }
        );
        this.enable_buttons( is_valid );
    },

    show_more_results: function ( e ) {
        // not implemented
    }

} );

$( document ).ready( function () {

    // patch
    $( '#addbysku-sku-num' ).removeClass( 'digits' );

    // Shop by Item Number Module
    let shopBySkuEl = $( '.shopBySku.module' );
    if ( shopBySkuEl[0] ) {
        new ShopBySku( shopBySkuEl );
    }

    // Recent Purchases Modules
    $( '.recentPurchases.module' ).each( function ( i, item ) {
        return (new RecentPurchases( item ));
    } );
} );




